.mainContainer {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  // padding: 0px 10px 0px 10px;
  margin-top: 10px;
}

.html {
  height: 95%;
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 50px 0px;
}

.markdown {
  height: 100%;
  width: 100%;
  margin-top: 20px;
  background-color: #f7f7f7;
  flex-wrap: wrap;
  overflow: scroll;

}

.markdownText {
  font-size: 20px;
  font-family: "Rubik";
  flex-wrap: wrap;
  padding: 20px;
  // height: 90%;

  // background: rebeccapurple;
  // margin-top: ;
}

.csv {
  font-size: 20px;
  font-family: "Rubik";
  flex-wrap: wrap;
  max-height: 90%;
  max-width: 100%;
  overflow-y: scroll;
  background-color: #f7f7f7;
  margin-top: 0px;
}

.image {
  font-size: 16px;
  font-family: "Rubik";
  padding: 20px;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  // background: rebeccapurple;
  // margin-top: ;
}

.image img {
  max-width: 70%;
  max-height: 70%;
  margin: auto;
}

.FullWidthSpinnerContainer {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

