.btnContainer {
  // position: absolute;
  top: 0%;
  height: 50px;
  width: 100%;
  display: flex;
  // background-color: #443E99; 
  justify-content: flex-end;
  align-items: center;
  margin: 0px 30px;
  padding: 10px 0px;

}

.downloadBtn {
  border: none;
  outline: none;
  height: 50px;
  min-width: 150px;
  border-radius: 5px;
  background-color: #443E99;
  color: #fbfbfc;
  font-family: 'Rubik';
  font-size: 16px;
  letter-spacing: 1px;
  padding: 0px 20px;
}

.downloadBtn:hover {
  transition: ease 300ms all;
  outline: none;
  background-color: #cfcfcf;
}